.settings {
    text-align: left;
    font-family: 'Bungee';
    padding: 10px;
    line-height: 0;
    max-width: 1600px;
    margin: 0 auto;
}

.settings * {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.settings .category {
    color: white;
    padding: 0 10px 0;
    margin: 20px 0 0;
}

.settings .category.first {
    margin-top: 0;
}

.settings .category > div {
    display: inline-block;
    _background-color: rgba(0, 0, 0, 0.3);
    padding: 10px 0 7px;
    margin: 0;
    border-bottom: 1px dashed rgba(0,0,0,0.5);
}

.settings .forms {
    padding: 10px;
    margin: 0 200px 0;
}

@media screen and (max-width: 1400px) {
    .settings .forms {
        padding: 10px;
        margin: 0 100px 0;
    }
}

@media screen and (max-width: 1200px) {
    .settings .forms {
        padding: 10px;
        margin: 0 50px 0;
    }
}

@media screen and (max-width: 1000px) {
    .settings .forms {
        padding: 10px;
        margin: 0 20px 0;
    }
}

@media screen and (max-width: 600px) {
    .settings .forms {
        padding: 0;
        margin: 0;

    }
}

.settings .forms .row {
    margin: 0;
    display: inline-block;
    width: 50%;
    padding: 5px 10px 0 10px;
}

@media screen and (max-width: 1100px) {
    .settings .forms .row {
        margin: 0;
        display: block;
        width: 100%;
        padding: 5px 10px 0 10px;
    }
}

.settings .forms .row.last {
    margin-bottom: 0;
}

.settings .forms .row > div {

}

.settings .forms .row > div.label {
    text-align: left;
    display: inline-block;
    padding: 6px 2px;
    font-size: 100%;
}

.settings .forms input {
    font-size: 10px;
    padding: 0 5px;
    border-radius: 3px;
    border: none;
    outline: none;
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: 1px solid transparent;
}

.settings .forms .xlog-size .xlog-label {
    display: inline-block !important;
    width: 70px;
    color: white;
    padding-left: 2px;
}

.settings .forms .xlog-size .xlog-label.second {
    margin-left: 20px;
}

.settings .forms .xlog-size select {
    padding: 0;
    width: calc(50% - 80px);
    border: none;
}

.settings .forms .xlog-size select[disabled] {
    background-color: rgba(0,0,0,0.3);
    color: white;
    border: none;
}

.settings .forms input[readonly] {
    background-color: transparent;
    background-color: rgba(0, 0, 0, 0.3);
    color: white;
    border: 1px solid rgba(0, 0, 0, 0.3);
}

.settings .buttons {
    padding: 15px 10px 0;
    text-align: center;
}

.settings .buttons button {
    padding: 12px 15px;
    border: none;
    background-color: white;
    margin: 0 2px;
    outline: none;
}

.settings .buttons button:hover {
    background-color: #EEE;
}

.settings .xlog-dot-config {
    display: table;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 3px;
    background-color: white;
    table-layout: fixed;
}

.settings.editable .xlog-dot-config {
    background-color: white;
}

.xlog-dot-config > div {
    display: table-cell;
    vertical-align: middle;
    text-align: center;
    padding: 10px;
}

.xlog-dot-config > div.xlog-dot-config-left {

}

.xlog-normal-dot {
    display: inline-block;
}

.xlog-normal-dot .xlog-dot-rows .xlog-dot-columns {
    width: 20px;
    height: 20px;
    background-color: rgba(0, 0, 0, 0.1);
    display: inline-block;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-right: none;
    border-bottom: none;
}

.settings.editable .xlog-normal-dot .xlog-dot-rows .xlog-dot-columns {
    cursor: pointer;

}

.xlog-normal-dot .xlog-dot-rows .xlog-dot-columns.last {
    border-right: 1px solid rgba(0, 0, 0, 0.2);
}

.xlog-normal-dot .xlog-dot-rows.last .xlog-dot-columns {
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.xlog-normal-dot .xlog-dot-rows .xlog-dot-columns.selected {
    border: 1px solid black;
}

.settings .xlog-dot-config .xlog-config-controller {
    position: relative;
    overflow-x: hidden;
}

.settings.editable .xlog-dot-config .xlog-config-controller {
    position: relative;
    overflow-x: auto;
}

.settings .xlog-dot-config .xlog-config-controller .disabled-wrapper {
    background-color: rgba(0, 0, 0, 0.3);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 0 3px 3px 0;
}

.settings.editable .xlog-dot-config .xlog-config-controller .disabled-wrapper {
    display: none;
}

.flexbox-fix input {
    padding: 5px 0 0 5px !important;

}




